import { request } from "http";
import { useEffect, useState } from "react";
import { FileText, Tv } from "react-feather";
import { FormattedMessage } from "react-intl";
import { useParams } from "react-router-dom";
import {
  Alert,
  Card,
  CardBody,
  Col,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import api from "../../../../../../../constants/api";
import CompanyInfomation from "./company_information";
import { getCompany } from "../store1/action";
import { useDispatch, useSelector } from "react-redux";
const CompanyInfomationEdit = () => {
  const [activeTab, setActiveTab] = useState("1"),
    store = useSelector((state) => state.company_information),
    dispatch = useDispatch(),
    { id } = useParams();
  const toggle = (tab) => setActiveTab(tab);

  useEffect(() => {
    dispatch(getCompany(parseInt(id)));
  }, [dispatch]);

  return store.selected !== null && store.selected !== undefined ? (
    <Row className="app-user-edit">
      <Col sm="12">
        <Card>
          <CardBody className="pt-2">
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === "1"} onClick={() => toggle("1")}>
                  <Tv size={14} />
                  <span className="align-middle d-none d-sm-block">
                    <FormattedMessage id="section_system" />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="1">
                <CompanyInfomation selected={store.selected} store={store} />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  ) : (
    <Alert color="danger">
      <h4 className="alert-heading">Not found</h4>
    </Alert>
  );
};
export default CompanyInfomationEdit;
