import { isObjEmpty } from "@utils";
import classNames from "classnames";
import draftToHtml from "draftjs-to-html";

import { convertToRaw, EditorState } from "draft-js";
import { useEffect, useState } from "react";
import { Editor } from "react-draft-wysiwyg";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";

import { useHistory, useParams } from "react-router-dom";
import { Button, Col, Form, FormGroup, Label, Row } from "reactstrap";
import avatarBlank from "./../../../../../../../assets/images/avatars/bg-blank.png";

import { useDispatch, useSelector } from "react-redux";
import {
  htmlToDraftUtil,
  scrollToTop,
} from "../../../../../../../utility/Utils";
import { updateCompany } from "../store1/action";
import {
  convertFileToBase64,
  uploadImage,
} from "../../../../../../../helper/common";

const CompanyInfomationTab = ({ selected, intl }) => {
  const store = useSelector((state) => state.company_information);
  const { register, errors, handleSubmit } = useForm();
  const history = useHistory();
  const [companyData, setCompanyData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const [status, setStatus] = useState(null);
  const [disable, setDisable] = useState(false);
  const { id } = useParams();

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const [valueDescription, setValueDescription] = useState(
    EditorState.createEmpty()
  );

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        companyData !== null &&
        selected.id !== companyData.id)
    ) {
      setCompanyData(selected);
    }
  }, [selected, companyData]);

  useEffect(() => {
    if (store.status === 200) {
      scrollToTop();
      // history.push("/apps/category/list");
      history.goBack();
    }
  }, [store.status, history]);

  const onSubmit = async (values) => {
    // console.log(values);
    const content = draftToHtml(
      convertToRaw(valueDescription.getCurrentContent())
    );
    if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        updateCompany({
          id: parseInt(id),
          data: {
            ...values,
            status: 1,
            content: content,
            lang,
          },
        })
      );
    }
  };
  const uploadCallback = async (file) => {
    var linkImg = avatarBlank;
    const imageBase64 = await convertFileToBase64(file);
    if (imageBase64) {
      const newData = imageBase64.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        const data = {
          imageData: newData[1],
          imageFormat: file?.type?.split("/")[1] || "png",
        };
        await uploadImage(data, file).then((res) => {
          linkImg = res?.data;
        });
      }
    }
    return new Promise((resolve, reject) => {
      resolve({ data: { link: linkImg } });
    });
  };

  // console.log(companyData?.description);
  useEffect(() => {
    if (companyData) {
      setStatus(companyData.status);
      setValueDescription(htmlToDraftUtil(companyData?.content || " "));
    }
  }, [companyData]);

  return (
    <Row>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="content">
              <FormattedMessage id="Description" />{" "}
              <span className="text-danger"></span>
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                // options: ["inline", "textAlign"],
                // inline: {
                //   inDropdown: false,
                //   options: ["bold", "italic", "underline"],
                // },
                image: { uploadCallback: uploadCallback },
              }}
              editorState={valueDescription}
              onEditorStateChange={(data) => setValueDescription(data)}
              name="content"
              // innerRef={register(ProductOptions.description_en)}
              className={classNames({
                "is-invalid": errors["content"],
              })}
            />
            <small className="text-danger">
              {errors?.description_en && errors.description_en.message}
            </small>
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CompanyInfomationTab);
