import api from "../../../../../../../../constants/api";
import Service from "../../../../../../../../services/request";

export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_COMPANY_INFOMATION.method,
      path: api.LIST_COMPANY_INFOMATION.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_COMPANY",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};

export const getCompany = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_COMPANY_INFOMATION.method,
      path: api.GET_COMPANY_INFOMATION.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_COMPANY",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};
export const updateCompany = (Company) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_COMPANY_INFOMATION.method,
      path: api.UPDATE_COMPANY_INFOMATION.path,
      data: Company,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_COMPANY",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_COMPANY",
          err,
        })
      );
  };
};
